/*need to do one for .active one navlink attribute works */

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
}

nav .title {
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: black;
}

nav .title:hover {
    transform: scale(1.15);
}

nav ul {
    display: flex;
}

nav ul li {
    list-style: none;
}

#a {
    display: block;
    text-decoration: none;
    color: black;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
}


/* psuedo selector so doesn't hightlight when on page note this wont work until link attribute is changed to navlink*/
#a:not(.active):hover {
    transform: scale(1.05);
}

nav .menu {
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}

nav .menu span {
    height: 0.4rem;
    width: 100%;
    background-color: black;
    border-radius: 0.2rem;
}

/* styling for small screen */
@media (max-width: 650px) {
    nav .menu {
        display: flex;
    }

    nav {
        flex-direction: column;
        align-items: start;
    }

    nav ul {
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }

    nav ul.open{
        display: flex;
    }

    nav ul li {
        width: 100%;
        text-align: center;
    }

    #a{
        margin: 0.2rem 0.5rem;
    }

}